%form-base {
    display: block;
    width: 100%;
    height: 40px;
    padding: 0 16px;
    border-radius: 4px;
    border: 1px solid $border;
    background-color: $white;
    color: $primary;
    &::placeholder {
        color: $tertiary;
    }
}

.select {
    @extend %form-base;
    appearance: none;
    &::after {
        content: '';
        display: block;
        // background: url('static/images/icon/Chevron.svg');
    }
    &:hover {
        background-color: $background;
    }

    &:active {
        border: 1px solid $blue;
    }

    &:disabled {
        opacity: 0.4;
    }
}

.form-input {
    @extend %form-base;
}
